@import url('https://fonts.googleapis.com/css2?family=VT323&display=swap');
* {
    box-sizing: border-box;
    font-smoothing: antialiased;
    -webkit-font-smoothing: antialiased;
}
button,
input,
optgroup,
select,
textarea {
    font-family: inherit;
    font-size: 100%;
    line-height: 1.15;
    margin: 0;
}

button,
input {
    overflow: visible;
}

button,
select {
    text-transform: none;
}

button,
[type="button"],
[type="reset"],
[type="submit"] {
    -webkit-appearance: button;
}

button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
    border-style: none;
    padding: 0;
}

button:-moz-focusring,
[type="button"]:-moz-focusring,
[type="reset"]:-moz-focusring,
[type="submit"]:-moz-focusring {
    outline: 1px dotted ButtonText;
}
body {
    font-family: 'VT323', monospace;
}
.pipesB {
    min-height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
}


/*pipes canvas*/
.pipesContainerView {
    flex-grow: 1;
    display: flex;
    max-height: calc(100vh - 100px * 2);
    overflow: auto;
    box-shadow: inset 0 0 20px 0 rgba(0, 0, 0, 0.45);
    border-bottom: 3px dashed;
    border-top: 3px dashed;
}
.pipesCanvasB {
    font-size: 0;
    font-weight: 700;
    font-family: serif;
    user-select: none;
    margin: auto;
}

.pipesLine {
    display: flex;
}
.partPipes {
    line-height: 1;
    font-size: 44px;
    cursor: pointer;
    overflow: hidden;
    width: 44px;
    height: 44px;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    outline: 1px solid #f2f2f2;
    text-align: center;
}

.partPipes:hover {
    background: #dadada6e;
}

.partPipes[rotate="90"] {
    transform: rotate(90deg);
}
.partPipes[rotate="180"] {
    transform: rotate(180deg);
}
.partPipes[rotate="270"] {
    transform: rotate(270deg);
}

.level1 .partPipes {
    font-size: 50px;
    width: 50px;
    height: 50px;
}

.level2 .partPipes {
    font-size: 30px;
    width: 30px;
    height: 30px;
}
.level3 .partPipes {
    font-size: 20px;
    width: 20px;
    height: 20px;
}
.level4 .partPipes {
    font-size: 15px;
    cursor: pointer;
    overflow: hidden;
    width: 15px;
    height: 15px;
}

/*control*/
.pipesControlB {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    margin: auto;
    padding: 10px 30px;
    height: 100px;
}

.pipesControlLvl {
    display: flex;
    align-items: center;
    line-height: 1;
    width: 250px;
}
.pipesControlLvl select {
    background: transparent;
    border: none;
    font-family: 'VT323', monospace;
    margin: 0 20px 0 5px;
    font-size: 48px;
    line-height: 1;
    outline: none;
    text-align: center;
}
.pipesControlLvl select option{
    font-size: 30px;
}
.pipesControlLvl span {
    font-weight: 600;
    font-size: 36px;
}

.pipesControlScore span {
    font-size: 46px;
}
.pipesControlScore {
    font-weight: 600;
    font-size: 36px;
}
.pipesControlHelp {
    display: flex;
    align-items: center;
    width: 150px;
    justify-content: flex-end;
}
.pipesControlHelp span{
    width: 20px;
    height: 20px;
    font-size: 30px;
    padding: 15px;
    border: 3px dashed;
    border-radius: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    transition: all 0.3s ease;
}
.pipesControlHelp span:hover {
    color: #fff;
    background: #000;
    border-color: #fff;
}

.pipesVerifyGame,
.pipesStartGame {
    background: #000;
    color: #fff;
    padding: 5px 20px;
    border: 2px dashed #fff;
    cursor: pointer;
    transition: all 0.3s ease;
    font-size: 30px;
}
.pipesVerifyGame:hover,
.pipesStartGame:hover {
    color: #000;
    background: #fff;
    border-color: #000;
}